import {PureComponent} from 'react';
import PropTypes from "prop-types";
import smoothScroll from 'smoothscroll';

export default class SmoothScroll extends PureComponent {
  static scrollTo(location) {
    setTimeout(() => {
      let target;

      if(location.state) {
        target = document.querySelector('.' + location.state.target);
      }

      if(target) {
        smoothScroll(target);
      } else {
        window.scrollTo(0,0);
      }
    }, 100);
  }

  componentDidMount() {
    this.removeListner = this.props.history.listen(SmoothScroll.scrollTo);
  }

  componentWillUnmount() {
    this.removeListner();
  }

  render() {
    return (null);
  }
}

SmoothScroll.propTypes = {
  history: PropTypes.object
};



