import React, {PureComponent} from 'react';
import './style.css';
import PropTypes from "prop-types";

export default class Contact extends PureComponent {
  render() {
    return (
        <div>
          <h5>{this.props.footerTitle}</h5>

          <ul className="contact">
            <li><i className="fa fa-phone"/>{this.props.phone}</li>
            <li><i className="fa fa-envelope"/>{this.props.email}</li>
            <li><i className="fa fa-map-marker"/>{this.props.location}</li>

            <ul className="social">
              {this.props.facebook && <li><a href={this.props.facebook}><i className="fa fa-facebook"/></a></li>}
              {this.props.linkedIn && <li><a href={this.props.linkedIn}><i className="fa fa-linkedin"/></a></li>}
            </ul>
          </ul>
        </div>
    );
  }
}

Contact.propTypes = {
  footerTitle: PropTypes.string.isRequired,
  phone: PropTypes.string.isRequired,
  email: PropTypes.string.isRequired,
  location: PropTypes.string.isRequired,
  linkedIn: PropTypes.string.isRequired,
  facebook: PropTypes.string,
};


