import React, {PureComponent} from 'react';
import PropTypes from 'prop-types';
import './style.css';
import CollaboratorsItem from "./CollaboratorsItem";

export default class CollaboratorsGroup extends PureComponent {
  render() {
    const items = this.props.items.map((data, index) => (
       <CollaboratorsItem key={index} {...data} isRight={index % 2 === 0}/>
    ));

    return (
        <section className="collaborators-section">
          <div className="section-title extra centerAlign">
            <p className="top">{this.props.preTitle}</p>
            <h2 className="title">{this.props.title}</h2>
            <p className="intro">{this.props.quote}</p>
          </div>

          <div className="container">
            <div className="row">
              {items}
            </div>
          </div>
        </section>
    );
  }
}

CollaboratorsGroup.propTypes = {
  preTitle: PropTypes.string,
  title: PropTypes.string,
  quote: PropTypes.string,
  groups: PropTypes.arrayOf(PropTypes.array)
};


