import React, {PureComponent} from 'react';
import PropTypes from 'prop-types';
import SliderSection from "./SliderSection/SliderSection";
import About from "./AboutSection/About";
import CollaboratorsSection from "./CollaboratorsSection/CollaboratorsSection";
import ContactSection from './ContactSection/ContactSection';

export default class MainPage extends PureComponent {
  render() {
    return (
      <div>
        <SliderSection sliderData={this.props.slider}
                       contactInfo={this.props.contactInfo}
                       contactLink={this.props.contactLink}
                       baseUrl={this.props.baseUrl}/>
        <About {...this.props.about}/>
        <CollaboratorsSection {...this.props.collaborators}/>
        <ContactSection contactForm={this.props.contact} contactInfo={this.props.contactInfo}/>
      </div>
    );
  }
}

MainPage.propTypes = {
  about: PropTypes.object.isRequired,
  slider: PropTypes.object.isRequired,
  collaborators: PropTypes.object.isRequired,
  contact: PropTypes.object.isRequired,
  contactInfo: PropTypes.object.isRequired,
  contactLink: PropTypes.object.isRequired,
  baseUrl: PropTypes.string.isRequired
};


