import React, {PureComponent} from 'react';
import PropTypes from 'prop-types';
import './style.css';

export default class CollaboratorsItem extends PureComponent {
  render() {
    const alignClass = this.props.isRight ? 'right' : 'left';

    return (
        <div className={`col-md-6 col-12 collaborators-item ${alignClass}`}>
          <span className="item-prefix mr-1">{this.props.prefix}</span>
          <span className="item-name">{this.props.name}</span>
        </div>
    );
  }
}

CollaboratorsItem.propTypes = {
  name: PropTypes.string,
  prefix: PropTypes.string,
  externalAddress: PropTypes.string,
  isRight: PropTypes.bool
};


