import React, {PureComponent} from 'react';
import './style.css';
import PropTypes from "prop-types";
import {Link} from 'react-router-dom';

export default class Links extends PureComponent {
  constructor(props) {
    super(props);

    this.renderLink = this.renderLink.bind(this);
  }

  renderLink(linkInfo) {
    const linkTo = {
      pathname: this.props.baseUrl + linkInfo.route,
      state: {target: linkInfo.target}
    };

    return <li>
      <Link to={linkTo}>
        <span>{linkInfo.displayName}</span>
      </Link>
    </li>
  }

  render() {
    return (
      <div>
        <h5>{this.props.footerTitle}</h5>
        <ul className="links">
          <div className="row">
            <div className="col-6">
              <ul>
                {this.renderLink(this.props.services.subRoutes.translation)}
                {this.renderLink(this.props.services.subRoutes.interpretation)}
                {this.renderLink(this.props.services.subRoutes.proofreading)}
                {this.renderLink(this.props.services.subRoutes.audit)}
              </ul>
            </div>
            <div className="col-6">
              <ul>
                {this.renderLink(this.props.contact)}
                {this.renderLink(this.props.aboutMe)}
                {this.renderLink(this.props.legalization)}
                {this.renderLink(this.props.collaborators)}
              </ul>
            </div>
          </div>
        </ul>
      </div>
    );
  }
}

Links.propTypes = {
  footerTitle: PropTypes.string.isRequired,

  legalization: PropTypes.object.isRequired,
  collaborators: PropTypes.object.isRequired,
  aboutMe: PropTypes.object.isRequired,
  contact: PropTypes.object.isRequired,
  services: PropTypes.shape({
    subRoutes: PropTypes.shape({
      translation: PropTypes.object,
      interpretation: PropTypes.object,
      proofreading: PropTypes.object,
      audit: PropTypes.object,
    }).isRequired
  }),
};


