import React, {PureComponent} from 'react';
import './style.css';

import PropTypes from 'prop-types';

export default class TranslationColumn extends PureComponent {
  render() {
    const getItems = items => {
      return items.map((item, index) => (
          <li key={index}>
            <span>{item}</span>
          </li>
      ));
    };

    return (
        <div className={`col-sm-6 col-12 ${this.props.className}`}>
          <h4 className="form-group">{this.props.title}</h4>
          <div className="row">
            <div className="col-12">
              <ul>
                {getItems(this.props.items)}
              </ul>
            </div>
          </div>
        </div>
    );
  }
}

TranslationColumn.propTypes = {
  title: PropTypes.string.isRequired,
  items: PropTypes.array.isRequired,
  className: PropTypes.string
};


