import React, {PureComponent} from 'react';
import PropTypes from 'prop-types';
import './style.css'

import img from '../../../../assets/images/proofreading_section.jpeg';
import SectionItem from "./SectionItem";


export default class ProofreadingSection extends PureComponent {
  render() {
    const items = this.props.sections.map((data, index) => (
        <SectionItem key={index} {...data}/>
    ));

    return (
        <section className="proofreading-section">
          <div className="main-content sectionBase featureList">
            <div className="inner">
              <div className="container">
                <div className="row">

                  <div className="col-lg-4 col-md-5 col-12">
                    <div className="sideInfo sideInfoAlt text-right">
                      <p><i>{this.props.quote}</i></p>
                      <p className="tight"><b>{this.props.quoteAuthor}</b></p>
                    </div>
                    <div className="sidePhoto">
                      <img src={img} className="responsive" alt={this.props.imageAlt}/>
                    </div>
                  </div>

                  <div className="col-lg-8 col-md-7 col-12">
                    <ul className="mb-0">
                      {items}
                    </ul>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>

    );
  }
}

ProofreadingSection.propTypes = {
  quote: PropTypes.string.isRequired,
  quoteAuthor: PropTypes.string.isRequired,
  imageAlt: PropTypes.string.isRequired,
  sections: PropTypes.array.isRequired,
};



