import React, {PureComponent} from 'react';
import PropTypes from 'prop-types';
import ReactHtmlParser from 'react-html-parser';
import './style.css'

import legalizationImg from '../../../../assets/images/legalizare.jpeg';


export default class LegalizationSection extends PureComponent {
  render() {
    return (
        <section className="container legalization-section">

          <div className="row">

            <div className="col-lg-4 col-md-12 order-lg-3">
              <div className="section-title">
                <p className="top">{this.props.preTitle}</p>
                <h2 className="title">{this.props.title}</h2>
              </div>
            </div>

            <div className="col-lg-4 col-md-6 order-lg-1 section-image">
              <img src={legalizationImg} className="responsive" alt={this.props.title}/>
            </div>

            <div className="col-lg-4 col-md-6 order-lg-2">
              <p>{ReactHtmlParser(this.props.text1)}</p>
              <p>{ReactHtmlParser(this.props.text2)}</p>
            </div>
          </div>
        </section>
    );
  }
}

LegalizationSection.propTypes = {
  preTitle: PropTypes.string.isRequired,
  title: PropTypes.string.isRequired,
  text1: PropTypes.string.isRequired,
  text2: PropTypes.string.isRequired,
};



