import React, {PureComponent} from 'react';
import './ContactInfo.css';
import PropTypes from 'prop-types';

export default class ContactInfo extends PureComponent {
  render() {
    return (
        <div className="contact-info d-none d-md-block">
          <div className="container">
            <div className="row">
              <ul className="col-sm-6 hidden-xs">
                <li>
                  <a href={this.props.facebook} rel="noopener"><i className="fa fa-facebook"/></a>
                </li>
                <li>
                  <a href={this.props.linkedIn} rel="noopener"><i className="fa fa-linkedin"/></a>
                </li>
              </ul>
              <ul className="col-sm-6 rightAlign xsAlignReset">
                <li>
                  <i className="fa fa-phone"/>
                  <span>{this.props.phone}</span>
                </li>
                <li>
                  <i className="fa fa-envelope"/>
                  <span>{this.props.email}</span>
                </li>
              </ul>
            </div>
          </div>
        </div>
    );
  }
}

ContactInfo.propTypes = {
  phone: PropTypes.string.isRequired,
  email: PropTypes.string.isRequired,
  facebook: PropTypes.string.isRequired,
  linkedIn: PropTypes.string.isRequired,
};


