import React, {PureComponent} from 'react';
import './SlideShow.css';
import Slider from 'react-slick';
import translationsImg from '../../../../../assets/images/traduceri_resized.jpg';
import interpretareImg from '../../../../../assets/images/interpretare_resized.jpg';
import PropTypes from 'prop-types';
import {Link} from 'react-router-dom';

export default class SlideShow extends PureComponent {
  constructor(props) {
    super(props);

    this.renderLink = this.renderLink.bind(this);
  }

  renderLink() {
    const linkTo = {
      pathname: this.props.baseUrl + this.props.contactLink.route,
      state: {target: this.props.contactLink.target}
    };

    return React.cloneElement(<Link to={linkTo}/>,
      {className: "button transparent large"},
      this.props.buttonText);
  }

  render() {

    return (
      <div className="slider">
        <Slider {...this.props.settings}>

          {/*Slide One*/}
          {/*DO NOT extract slides in a separate component because slick adds wrapper elements and slider will not work*/}
          <div className="slider__item">
            <div className="item__image img item__image--first">
              <img src={translationsImg} alt={this.props.translationSlide.imgAlt}/>
            </div>
            <div className="item__description">
              <div className="item__description__title">
                <p>{this.props.title}</p>
                <h1>{this.props.translationSlide.header1}
                  <span className="break">{this.props.translationSlide.header2}</span>
                </h1>
              </div>
              <div className="item__description__buttons">
                {this.renderLink()}
              </div>
            </div>
          </div>

          {/*Slide Two*/}
          <div className="slider__item">
            <div className="item__image img item__image--first">
              <img src={interpretareImg} alt={this.props.interpretationSlide.imgAlt}/>
            </div>
            <div className="item__description">
              <div className="item__description__title">
                <p>{this.props.title}</p>
                <h1>{this.props.interpretationSlide.header1}</h1>
              </div>
              <div className="item__description__buttons">
                {this.renderLink()}
              </div>
            </div>
          </div>

        </Slider>
      </div>
    );
  }
}

SlideShow.propTypes = {
  title: PropTypes.string.isRequired,
  buttonText: PropTypes.string.isRequired,
  translationSlide: PropTypes.object.isRequired,
  interpretationSlide: PropTypes.object.isRequired,
  contactLink: PropTypes.object.isRequired,
  baseUrl: PropTypes.string.isRequired
};


