import React, {PureComponent} from 'react';
import PropTypes from 'prop-types';

import './style.css';
import ContactForm from './ContactForm';

export default class ContactSection extends PureComponent {
  render() {
    return (
        <section className="greyWrapper contact-section">
          <div className="container">
            <div className="row">

              {/*about text*/}
              <div className="col-md-6 col-sm-12 order-md-1 order-2">
                <ContactForm {...this.props.contactForm}/>
              </div>

              {/*contact info*/}
              <div className="col-md-5 offset-md-1 col-sm-12 order-md-2 order-1 company-info-wrapper">
                <h2 className="text-right">Contact</h2>
                <div className="company-info mb-5">
                  <ul>
                    <li><a href="tel:+447893201">
                      <i className="fa fa-phone fa-fw"/>{this.props.contactInfo.phone}
                    </a></li>
                    <li><i className="fa fa-envelope fa-fw"/>{this.props.contactInfo.email}</li>
                    <li><i className="fa fa-map-marker fa-fw"/>{this.props.contactInfo.location}</li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </section>
    );
  }
}

ContactSection.propTypes = {
  contactForm: PropTypes.object.isRequired,
  contactInfo: PropTypes.shape({
    phone: PropTypes.string,
    email: PropTypes.string,
    location: PropTypes.string
  }).isRequired
};




