import React, {PureComponent} from 'react';
import PropTypes from 'prop-types';

export default class SectionItem extends PureComponent {
  render() {
    return (
        <li className="item">
          <div className="icon"><i className={`fa ${this.props.faIconClass}`} aria-hidden="true"/></div>
          <h5>{this.props.title}</h5>
          <p>{this.props.text}</p>
          {this.props.text2 && <p>{this.props.text2}</p>}
        </li>

    );
  }
}

SectionItem.propTypes = {
  title: PropTypes.string.isRequired,
  faIconClass: PropTypes.string.isRequired,
  text: PropTypes.string.isRequired,
  text2: PropTypes.string.isRequired,
};



