import React, {PureComponent} from 'react';
import PropTypes from 'prop-types';

import './style.css';

export default class ContactForm extends PureComponent {
  render() {
    return (
        <form className="" method="post" action="test.php">
          <input type="text" required autoComplete="name"
                 className="form-control form-group" name="name" placeholder={this.props.namePlaceholder}/>
          <input type="email" required autoComplete="email"
                 className="form-control form-group" name="email" placeholder={this.props.emailPlaceholder}/>
          <input type="text" required autoComplete="off"
                 className="form-control form-group" name="subject" placeholder={this.props.subjectPlaceholder}/>
          <div className="row">
            <div className="col-12 col-sm-9">
              <textarea rows="3" required autoComplete="off"
                        className="form-control mb-3 mb-sm-0" name="message" placeholder={this.props.messagePlaceholder}/>
            </div>
            <div className="col-12 col-sm-3 button-container">
              <button type="submit" className="button dark">{this.props.buttonText}</button>
            </div>
          </div>
        </form>
    );
  }
}

ContactForm.propTypes = {
  namePlaceholder: PropTypes.string.isRequired,
  emailPlaceholder: PropTypes.string.isRequired,
  subjectPlaceholder: PropTypes.string.isRequired,
  messagePlaceholder: PropTypes.string.isRequired,
  buttonText: PropTypes.string.isRequired,
  sendSuccessMessage: PropTypes.string.isRequired,
  sendErrorMessage: PropTypes.string.isRequired,
};




