import React, {PureComponent} from 'react';
import PropTypes from 'prop-types';
import './Header.css';
import Navbar from './Navbar/Navbar';
import logo from '../../../assets/images/logo_baltag_3.png';

export default class Header extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      menuExpanded: false,
    };

    this.toggleMenuExpanded = this.toggleMenuExpanded.bind(this);
  }

  toggleMenuExpanded() {
    this.setState(prevState => ({menuExpanded: !prevState.menuExpanded}));
  }

  render() {
    const headerFixed = this.props.headerInverse ? 'fixed' : '';

    return (
        <header className={`mastHead ${headerFixed}`}>
          <div className="mobileNavBtn d-block d-lg-none" onClick={this.toggleMenuExpanded}>
            <i className="fa fa-bars fa-lg"/>
          </div>

          <div className="header__content">
            <div className="row">
              <div className="col-lg-1 logo-parent">
                <div className="logo">
                  <a href="/">
                    <img src={logo} alt={this.props.data.logo.alt}/>
                  </a>
                </div>
              </div>
              <div className="col-lg-11">
                <Navbar navItems={this.props.data.navItems}
                        menuExpanded={this.state.menuExpanded}
                        toggleMenuExpanded={this.toggleMenuExpanded}
                        baseUrl={this.props.baseUrl}/>
              </div>
            </div>
          </div>
        </header>
    );
  }
}

Header.propTypes = {
  headerInverse: PropTypes.bool.isRequired,
  baseUrl: PropTypes.string,

  data: PropTypes.shape({
    navItems: PropTypes.object.isRequired,
    languages: PropTypes.array.isRequired,
    logo: PropTypes.shape({alt: PropTypes.string.isRequired}),
    contactInfo: PropTypes.object.isRequired,
  }),
};


