import React, {PureComponent} from 'react';
import debounce from 'lodash/debounce';
import {BrowserRouter as Router, Redirect, Route, Switch} from "react-router-dom";

import './style.css';
import dataRo from "./i18n/app.data.ro";
import dataEn from "./i18n/app.data.en";
import dataPt from "./i18n/app.data.pt";

import App from "./App/App";

export default class AppWrapper extends PureComponent {
  debounceScrollChange = debounce(this.handleScrollChange, 100, {maxWait: 150});

  constructor() {
    super();

    this.state = {
      isMobile: AppWrapper.isMobileView(),
      headerInverse: false,
      backToTopHidden: true,
    };

    this.handleWindowSizeChange = this.handleWindowSizeChange.bind(this);
    this.handleScrollChange = this.handleScrollChange.bind(this);

    this.debounceScrollChange = this.debounceScrollChange.bind(this);
  }

  static isMobileView() {
    //Keep in sync with $breakpoint-mobile from vars
    return window.innerWidth <= 767;
  }

  componentWillMount() {
    window.addEventListener('resize', this.handleWindowSizeChange);
    window.addEventListener('scroll', this.debounceScrollChange);
  }

  componentDidMount() {
    document.getElementById('preloader').classList.add('hidden');
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.handleWindowSizeChange);
    window.removeEventListener('scroll', this.debounceScrollChange);
  }

  handleWindowSizeChange() {
    this.setState({isMobile: AppWrapper.isMobileView()});
  };

  handleScrollChange() {
    const scrollPosition = window.pageYOffset || document.documentElement.scrollTop;
    this.setState({headerInverse: scrollPosition > 0});
  }

  render() {
    return (
        <Router>
          <Switch>
            <Route path="/en" render={props => <App {...dataEn} {...this.state} routeProps={props}/>}/>
            <Route path="/pt" render={props => <App {...dataPt} {...this.state} routeProps={props}/>}/>
            <Route path="/ro" render={props => <App {...dataRo} {...this.state} routeProps={props}/>}/>
            <Redirect from='/' to='/ro'/>
          </Switch>
        </Router>
    );
  }
}



