import React, {PureComponent} from 'react';
import PropTypes from 'prop-types';
import './style.css'

export default class InterpretationSection extends PureComponent {
  render() {
    return (
        <section className="container interpretation-section">
          <div className="row">

            <div className="col-lg-6 col-md-12">
              <div className="section-title">
                <p className="top">servicii</p>
                <h2 className="title">Interpretariat</h2>
              </div>

              <p>
                Servicii de Interpretare autorizat n faa autoritilor romne, persoanelor fizice i juridice
                strine, precum notari publici, instane de judecat i birouri de avocatur.
              </p>
              <p>
                Activiti de interpretariat oferite la intlniri i prnzuri de afaceri, pentru grupuri aflate n
                vizita, conferinte, congrese, seminarii, etc.
              </p>
              <p>
                Interpretarea se poate efectua n Cluj i n localitile lemitrofe i necesita o programare
                din timp.
              </p>
            </div>

            <div className="col-lg-6 col-md-12">
              <div className="row">

                <div className="col-12 col-md-6 col-lg-12 iconBox">
                  <div>
                    <div className="icon">
                      <i className="fa fa-superpowers"/>
                    </div>
                    <h5>Interpretariat consecutiv</h5>
                  </div>
                  <p>Interpretul traduce discursul vorbitorului cu ajutorul
                    notitelor astfel incat traducerea sa ramana fidela textului sursa.</p>
                </div>

                <div className="col-12 col-md-6 col-lg-12 iconBox">
                  <div className="icon"><i className="fa fa-comments-o"/></div>
                  <h5>Chuchotage-ul</h5>
                  <p>Presupune ca interpretul sa stea langa ascultatori soptindu-le traducerea
                    discursului oratorului. Reprezinta o forma a traducerii simultane prezentand avantajul ca nu
                    necesita utilizarea dispozitivelor tehnice.</p>
                </div>
              </div>
            </div>
          </div>
        </section>
    );
  }
}

InterpretationSection.propTypes = {
  test: PropTypes.string
};



