import React, {PureComponent} from 'react';
import './About.css';

//Todo change picture after Mihaela gives me one
import avatar from '../../../../assets/images/legalizare.jpeg';
import PropTypes from 'prop-types';
import ReactHtmlParser from "react-html-parser";

export default class About extends PureComponent {
  render() {
    return (
        <section className="greyWrapper about-section">
          <div className="container">
            <div className="row">

              {/*about text*/}
              <div className="col-md-7 col-sm-12 mBase25">
                <div>
                  <div className="sectionTitle">
                    <p>About</p>
                    <h2 className="title">{this.props.title}</h2>
                  </div>
                  <p>{ReactHtmlParser(this.props.text1)}</p>
                  <p className="mBase40">{this.props.text2}</p>
                  <p className='strong-points-list'>
                    <ul>{this.props.strongPoints.map((item, key) => {return key % 2 === 0 ? <li key={key}>{item}</li> : null})}</ul>
                    <ul>{this.props.strongPoints.map((item, key) => {return key % 2 !== 0 ? <li key={key}>{item}</li> : null})}</ul>
                  </p>
                </div>
              </div>

              {/*contact info*/}
              <div className="col-md-4 offset-md-1 col-sm-12 image-container">
                <img src={avatar} alt={this.props.imageAlt} title={this.props.imageAlt}/>
              </div>
            </div>
          </div>
        </section>
    );
  }
}

About.propTypes = {
  title: PropTypes.string.isRequired,
  text1: PropTypes.string.isRequired,
  text2: PropTypes.string.isRequired,
  strongPoints: PropTypes.arrayOf(PropTypes.string).isRequired,
  imageAlt: PropTypes.string.isRequired
};




