import React, {PureComponent} from 'react';
import PropTypes from 'prop-types';
import {Link} from 'react-router-dom'

export default class NavLink extends PureComponent {
  closeMenu(event) {
    event.stopPropagation();
    this.props.closeMenu();
  }

  renderNavLink() {
    const linkTo = {
      pathname: this.props.route,
      state: {target: this.props.target}
    };

    return (
      <li onClick={this.closeMenu.bind(this)}>
        <Link to={linkTo}>
          <span>{this.props.displayName}</span>
        </Link>
      </li>
    );
  }

  renderComposedNavLink() {
    return (
      <li onClick={this.props.toggleExpanded}>
        <a>
          <span>{this.props.displayName}</span>
          <i className="fa fa-angle-down"/>
        </a>
        {this.props.children}
      </li>
    );
  }

  render() {
    return !this.props.children ? this.renderNavLink.bind(this)() : this.renderComposedNavLink.bind(this)();
  }
}

NavLink.propTypes = {
  toggleExpanded: PropTypes.func,
  closeMenu: PropTypes.func,
  displayName: PropTypes.string,
  target: PropTypes.string,
  route: PropTypes.string
};



