import React, {PureComponent} from 'react';
import './SliderSection.css';
import SlideShow from './SlideShow/SlideShow';
import ContactInfo from './ContactInfo/ContactInfo';
import PropTypes from 'prop-types';

export default class SliderSection extends PureComponent {
  render() {
    let sliderSettings = {
      dots: true,
      fade: true,
      arrows: false,
      infinite: true,
      speed: 500,
      slidesToShow: 1,
      slidesToScroll: 1,
      autoplay: true,
      autoplaySpeed: 5000,
    };

    return (
        <div className="slider-section">
          <SlideShow settings={sliderSettings}
                     {...this.props.sliderData}
                     contactLink={this.props.contactLink}
                     baseUrl={this.props.baseUrl}/>
          <ContactInfo {...this.props.contactInfo}/>
        </div>
    );
  }
}

SliderSection.propTypes = {
  contactInfo: PropTypes.object.isRequired,
  sliderData: PropTypes.object.isRequired,
  baseUrl: PropTypes.string.isRequired,
  contactLink: PropTypes.object.isRequired
};


