import React, {PureComponent} from 'react';
import ReactHtmlParser from "react-html-parser";
import PropTypes from 'prop-types';


import './style.css';
import TranslationColumn from './TranslationColumn/TranslationColumn';

export default class TranslationSection extends PureComponent {
  render() {

    return (
        <section className="greyWrapper translation-section">
          <div className="container">
            <div className="row">

              <div className="col-12 col-lg-5 translation__header">
                <div className="section-title">
                  <p>Servicii</p>
                  <h2 className="title">{ReactHtmlParser(this.props.mainHeader)}</h2>
                  <h4>{this.props.secondHeader}</h4>
                </div>
                <p>{this.props.mainText1}</p>
                <p>{this.props.mainText2}</p>
              </div>

              <div className="col-10 col-lg-6 offset-1 translation__body">
                <div className="row">
                  <TranslationColumn {...this.props.juridic} className='judicial'/>
                  <TranslationColumn {...this.props.general} className='general mt-4 mt-sm-0'/>
                </div>
              </div>

            </div>
        </div>
        </section>
    );
  }
}

TranslationSection.propTypes = {
  mainText1: PropTypes.string.isRequired,
  mainText2: PropTypes.string.isRequired,
  mainHeader: PropTypes.string.isRequired,
  secondHeader: PropTypes.string.isRequired,
  juridic: PropTypes.shape({
    title: PropTypes.string.isRequired,
    items: PropTypes.array.isRequired,
  }),
  general: PropTypes.shape({
    title: PropTypes.string.isRequired,
    items: PropTypes.array.isRequired,
  })
};


